export const removeItem = (state, item) => {
  if (state.cart[item.name].count <= 1) {
    state.cartTotal--
    let none = state.cart[item.name].count = 0
    delete state.cart[item.name]
    return none
  } else {
    state.cart[item.name].count--
    state.cartTotal--
    return state.cart
  }
}
export const removeCart = (state) => {
  state.cart = []
  state.cartTotal = 0
}



